<template>
    <div class='yimin'>
        <h></h>
        <div class='yiminMain'>
            <div class='ban' id='liuxueB'></div>
           <div class='yiminDetBox'>
               <div class='w1200 posiRle' v-for='item in itemInfoArr'>
                   <div class='yiminDTop shadow'>
                       <div class='fl lunbo'>
                           <!-- banner -->
                           <el-carousel :interval="5000" arrow="always"  v-if='fileList.length>1'>
                                <el-carousel-item  v-for='(b,index) in fileList'>
                                 <img :src='b.filePath'/>
                                </el-carousel-item>
                            </el-carousel>
                          <img :src='b.filePath' v-for='(b,index) in fileList' v-if='fileList.length==1'/>
                       </div>
                       <div class='fl lunText'>
                           <p class='tit' v-html='item.itemTitle'></p>
                           <p class='intr' v-html='item.lightspot'></p>
                          <p class='type'><span class='fl'>{{item.target}}</span></p>
                          <p class='type'><span class='fl'>{{item.cycle}}</span></p>
                          <p class='pric'>&nbsp;&nbsp;<span>{{item.amountSum}}</span>&nbsp;</p>
                          <p class='yuyueP'><span class='yuyueBtn fl btnHoverZ' @click='toAppont()'></span><span class='fl tel'><img src='./img/appTel.png'/>010-52705440</span></p>
                       </div>
                   </div>

                   <div class='yiminDBottom shadow'>
                       <ul class='maoUl'>
                           <a href="#advantage"><li class='active' id='nav1'><span></span></li></a>
                           <a href="#apply_cond"><li class='' id='nav2'><span></span></li></a>
                           <a href="#cost_det"><li class='' id='nav3'><span></span></li></a>
                           <span class='yuyueBtn fr btnHoverZ' @click='toAppont()'></span>
                       </ul>
                       <div id='Project_intr' class='nav1'>
                            <!--  -->
                            <ul class='detailUl'>
                                <li><p class='nor' v-html='item.describe'></p></li>
                                <!-- <li>
                                    <p class='tit'></p>
                                    <p class='nor'>20134201579252125201763045,7791,6844,09510</p>
                                </li> -->
                                <li id='advantage'>
                                    <p class='tit'></p>
                                    <p class='nor' v-html='item.superiority'></p>
                                </li>
                            </ul>
                       </div>
                   </div>
                   <div  class='shadow' >
                       <span id='apply_cond' style='position:relative;top:-120px;opacity:0;font-size:10px;' class='nav2'>1</span>
                            <!--  -->
                            <ul class='detailUl'>
                                <li class='fenLi'>
                                    <p class='tit'></p>
                                     <p class='nor' v-html='item.applyCondition'></p>
                                     <!-- <p class='nor'>2.25</p>
                                     <p class='nor'>3.</p>
                                     <p class='nor'>4.</p> -->
                                </li>
                                <li class='fenLi'>
                                    <p class='tit'></p>
                                     <p class='nor'  v-html='item.applyProcess'></p>
                                     <!-- <p class='nor'>3.EOI</p>
                                     <p class='nor'>4.</p>
                                     <p class='nor'>5.Visa/MasterCard</p>
                                     <p class='nor'>6.20%-30%</p> -->
                                </li>
                            </ul>
                    </div>
                    <div  class='shadow' >
                        <span id='cost_det' style='position:relative;top:-120px;opacity: 0;font-size:10px;' class='nav3'>1</span>
                        <!--  -->
                        <ul class='detailUl'>
                            <li class='fenLi'>
                                <p class='tit'></p>
                                <p class='nor'  v-html='item.totalCost'></p>
                                <!-- <p class='nor'>CNY40,000()</p>
                                <p class='nor'>40</p>
                                <p class='nor'>1430%</p>
                                <p class='nor'>201970%</p> -->

                                <!-- <p class='qtFei'></p>
                                <p class='nor'>4,000</p>
                                <p class='nor'>1%</p>
                                <p class='nor'>250/</p>
                                <p class='nor'>45</p> -->




                            </li>
                        </ul>
                    </div>
               </div>
           </div>
        </div>
        <sidebar></sidebar>
        <appointment ref='c1'></appointment>
        <f></f>
    </div>
</template>
<script>
import h from '@/components/header/header.vue'
import f from '@/components/footer/footer.vue'
import sidebar from '../../components/sidebar/sidebar.vue'
import appointment from '../../components/appointment/appointment.vue'
import axios from 'axios'
export default {
    name:'yimin',
    data:function(){
        return{
            id:'',//Id
            itemInfoArr:[],
            fileList:[],
            proId:'',//Id
            name:'',
            ifShow:true,
            }
    },
    components:{sidebar,appointment,f,h},
    methods:{
        getInfo:function(id){
            var that=this;
            var obj={"FLAG":"2","ITEM_ID":id}
            axios.post('/trpch/trcrm/item/queryItemInfo',obj)
            .then(res=>{
                var data=res.data;
                console.log(data);
                if(data.isSuccess==1){
                   var itemInfo=data.itemInfo;
                   that.itemInfoArr.push(itemInfo);
                    that.fileList=data.fileList;
                    that.proId=itemInfo.proId;
                   that.name=itemInfo.itemTitle;
                }     
            })
        },
        toAppont:function(){
            var that=this;
            that.$refs.c1.toShow(that.name,that.id,that.ifShow,true);
        }
    },
    created:function(){
        var that=this;
        that.id=that.$route.query.id;
        if(!that.id==false){
           that.getInfo(that.id)//
        }
        
    }
}
</script>
<style>
 #liuxueB{
        background-image: url(img/liuxuebanner.jpg);
    }
</style>




